import { useRef, useState, useEffect } from 'react';

import PLACE_HOLDER from './images/placeholder-768x432.jpg';
import classNames from 'classnames';

interface LazyLoadImageProps {
  placeHolder?: string;
  src: string;
  alt: string;
  width?: string | number;
  height?: string | number;
}

const LazyLoadImage = ({
  placeHolder = PLACE_HOLDER,
  src,
  alt,
  width = 300,
  height = 300
}: LazyLoadImageProps) => {
  const imgRef = useRef<HTMLImageElement>(null);
  const [isLoad, setIsLoad] = useState(false);

  useEffect(() => {
    const loadImage = () => {
      setIsLoad(true);
    };

    const imgEl = imgRef.current;
    imgEl && imgEl.addEventListener(LOAD_IMG_EVENT_TYPE, loadImage);
    return () => {
      imgEl && imgEl.removeEventListener(LOAD_IMG_EVENT_TYPE, loadImage);
    };
  }, []);

  useEffect(() => {
    if (!observer) {
      observer = new IntersectionObserver(onIntersection, {
        threshold: 0.1
      });
    }
    imgRef.current && observer.observe(imgRef.current);
  }, []);

  return (
    <img
      ref={imgRef}
      src={isLoad ? src : placeHolder}
      alt={alt}
      width={width}
      height={height}
      className={classNames('inline-flex items-center justify-center', {
        'bg-solid-paper object-none blur-sm': !isLoad
      })}
    />
  );
};

let observer: IntersectionObserver | null = null;
const LOAD_IMG_EVENT_TYPE = 'loadImage';

const onIntersection = (entries: IntersectionObserverEntry[], io: IntersectionObserver) => {
  entries.forEach((entry) => {
    if (entry.isIntersecting) {
      io.unobserve(entry.target);
      entry.target.dispatchEvent(new CustomEvent(LOAD_IMG_EVENT_TYPE));
    }
  });
};

export default LazyLoadImage;
